// src/router/index.js
import { createRouter, createWebHistory } from 'vue-router'; // Changed import
import Hero from '../components/Hero.vue';

const routes = [
  {
    path: '/',
    name: 'Hero',
    component: Hero,
  },
];

const router = createRouter({
  history: createWebHistory(), // Changed here
  routes,
});

export default router;